@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100%;
  font-family: "Lato", sans-serif;
}

h1 {
  position: absolute;
  top: 50px;
  left: 0;
  right: 0;
  text-align: center;
}

.container {
  display: -webkit-flex;
  display: flex;
}

